<template>
    <div class="loginWrap">
        <img src="../../assets/login/bg3.png">
        <div class="log">
            <div class="login">
                <h3>应急演练系统平台</h3>

                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item prop="user" >
                    <div class="pic"><i class="iconfont el-icon-emergency-zhanghao"></i></div>
                    <el-input v-model="ruleForm.user" placeholder="账号" @keyup.native="getDepart(ruleForm.user)"></el-input>
                </el-form-item>


                <el-form-item prop="department" style="margin-right: 58px;height: 36px;" >
                    <el-select v-model="ruleForm.department" placeholder="选择部门">
                        <el-option :label="item" :value="item" v-for="(item,keys) in departs" :key="keys" ></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item prop="password">
                    <div class="pic"><i class="iconfont el-icon-emergency-mima"></i></div>
                    <el-input v-model="ruleForm.password" placeholder="密码" type="password"  @keyup.enter.native="submitForm('ruleForm')"></el-input>
                </el-form-item>

                <!--<el-form-item style="float: left;margin-left: -100px;margin-top: -10px;">-->
                <!--<el-checkbox label="记住密码" v-model="checked" :checked="checked"></el-checkbox>-->
                <!--</el-form-item>-->

                <!--<div class="forget"><button>忘记密码?</button></div>-->

                </el-form>

                <button @click="submitForm('ruleForm')" class="btnLogin">登录</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                departsId:[],//部门id
                departs:[],//部门选项
                departInfo:'',//部门信息
                token:'',
                checked: false,//记住密码勾选状态
                ruleForm: {//用户信息表单
                    user: '',//用户账号
                    department: '',//选中的部门
                    password:'',//密码
                    type:[],
                },
                rules: {//表单规则
                    user: [{required: true, message: '请输入账号', trigger: 'blur'},],
                    department: [{required: true, message: '请选择部门', trigger: 'change'}],
                    password:[{required: true, message: '请输入密码', trigger: 'blur'}],
                    type:[{trigger: 'blur'}]
                }
            }
        },
        created(){
        },

        //页面加载调用获取cookie值
        mounted(){
            this.getCookie();
        },
        methods: {
            //获取部门
            getDepart(name){
                this.departInfo = [];
                this.departs = [];
                if(name!=''){
                    this.$axios.post('/api/my-section',{
                        "name":name
                    }).then((res)=> {
//                  console.log(res);
                    let arr = [];
                    this.departInfo=res.data.data;
                    for(let i = 0;i<res.data.data.length;i++){
                        arr.push(res.data.data[i].section.section_one+res.data.data[i].section.section_two);
                    }
                        this.departs = arr;
                    }).catch((error)=> {
//                  console.log(error);
                    })
                } else {
                    //用户账号为空时
                    this.departInfo = [];
                    this.departs = [];
                    this.ruleForm.department = '';
                }
            },

        //登录
        submitForm(ruleForm){
            this.$refs[ruleForm].validate((valid) => {
                if(valid){
                    for(let i=0;i<this.departInfo.length;i++){
                        if((this.departInfo[i].section.section_one+this.departInfo[i].section.section_two)==this.ruleForm.department){
                            this.departsId=this.departInfo[i].section_id;
                        }
                    }

                    //保存的账号
                    var name=this.ruleForm.user;
                    //保存的部门
                    var dept=this.ruleForm.department;
                    //保存的部门Id
                    var deptId=this.departsId;
                    //保存的密码
                    var pass=this.ruleForm.password;
                    var chec=this.checked;

                    //判断复选框是否被勾选 勾选则调用配置cookie方法
                    if(this.checked==true){
                        //传入账号名，部门，密码
                        this.setCookie(name,dept,deptId,pass);
                        window.document.cookie="usercheck"+"="+"true"+";path=/;";
                        this.getCookie();
                    } else {
                        this.clearCookie();
                        window.document.cookie="usercheck"+"="+"false"+";path=/;";
                        //清除cookie
                    }

                    this.$axios.post('/api/login',{
                        "section_id":this.departsId,
                        "name":this.ruleForm.user,
                        "password":this.ruleForm.password
                    }).then((res)=> {
//                    this.token=res.data.data.token;
////                    console.log(this.token);
//
//                    this.$axios.post('/api/jwt-token',{
//                      "token":this.token
//                    })
//                      .then((res)=> {
//                        console.log(res)
//                      })
//                      .catch((error)=> {
//                        console.log(error);
//                      })
                        if (res.data.status == "0") {
                        localStorage.setItem("user", JSON.stringify(res.data.data));

                        this.$router.push('/');
                        } else {
                            this.$message({
                                message: res.data.message,
                                type: 'warning'
                            });
                        }
                    }).catch((error)=> {
                        console.log(error);
                    })
                }
                else{
                    return false;
                }
            })
        },

        //设置cookie
        setCookie(c_name,c_dept,c_deptId,c_pass) {
        //字符串拼接cookie
            window.document.cookie="userName"+ "=" +c_name+";path=/;";
            window.document.cookie="userdept"+ "=" +c_dept+";path=/;";
            window.document.cookie="userdeptId"+ "=" +c_deptId+";path=/;";
            window.document.cookie="userPwd"+"="+c_pass+";path=/;";
        },
        //读取cookie
        getCookie() {
            if (document.cookie.length>0) {
                var arr=document.cookie.split('; ');//格式切割
                //            console.log(arr)
                for(var i=0;i<arr.length;i++){
                    var arr2=arr[i].split('=');//再次切割
                    //判断查找相对应的值
                    if(arr2[0]=='userName'){
                        this.ruleForm.user=arr2[1];//保存到保存数据的地方
                    }else if(arr2[0]=='userdept'){
                        this.ruleForm.department=arr2[1];
                    } else if(arr2[0]=='userdeptId'){
                        this.departsId=arr2[1];
                    } else if(arr2[0]=='userPwd'){
                        this.ruleForm.password=arr2[1];
                    }else if(arr2[0]=='usercheck'){
                        this.checked=arr2[1];
                    }
                }
            }
        },
        //清除cookie
        clearCookie() {
            this.setCookie("","","","");
        }
        },
    }
</script>

<style lang="less" rel="stylesheet/less">
    .loginWrap {
        min-width: 1000px;
        width: 100%;
        /*height: 1000px;*/
        >img{
            width: 100%;
            height: auto;
            display: block;
        }
       .log {
         position: absolute;
         top: 302px;
         left: 50%;
         margin-left: -249px;
         border-radius: 5px;
         padding: 12px 27px 27px 27px;
         background-color: rgba(255, 255, 255, 0.4);

         .login {
           width: 443px;
           height: 331px;
           border-radius: 5px;
           background-color: #fff;
           background-clip: padding-box;
           margin: 0 auto;
           position: relative;

           h3 {
             font-size: 30px;
             font-weight: bold;
             text-align: center;
             color: #4d4d4d;
             margin: 15px auto;
             padding-top: 15px;
           }
         }
           .pic {
             position: absolute;
             top: -1px;
             left: -43px;
             border: 1px solid #bfbfbf;
             height: 36px;
             width: 324px;
             i {
               font-size: 16px;
               margin-left: 16px;
             }
           }

           .forget {
             float: right;
             margin-right: 61px;
             margin-top: 0px;
             font-size: 14px;
             button{
               color: #3fb6eb;
               text-decoration: none;
               background-color: #fff;
               border:0;
               padding: 0;
             }
           }
           .btnLogin {
             width: 323px;
             height: 36px;
             border-radius: 2px;
             background-image: linear-gradient(to bottom, #67bbf4, #2ab3e6);
             font-size: 16px;
             color: #ffffff;
             text-align: center;
             margin: 0 auto;
             position: absolute;
             left: 60px;
             bottom: 20px;
           }
       }


      .el-form-item{
        float: right;
        margin-right: 60px;
        padding-left: 60px;
        margin-bottom: 20px;
        height: 36px;
      }

      .el-input__inner{
        border-radius: 0;
        border:0;
        width:281px;
        box-sizing:border-box;
        -moz-box-sizing:border-box; /* Firefox */
        -webkit-box-sizing:border-box; /* Safari */;
        height:36px;
      }

      .el-select {
        display: inline-block;
        position: relative;
        width: 324px;
        border: 1px solid #bfbfbf;

      }

      .el-form-item__content{
        line-height: 33px;
      }

      .el-form-item__error:nth-child(odd){
        left:-43px;
      }

        /**/



   }

    /*.el-select-dropdown.el-popper{*/
    /*position: absolute !important;*/
    /*left:50% !important;*/
    /*margin-left: 169px !important ;*/
    /*}*/
    .iconfont {
      font-family: "iconfont" !important;
    }
</style>






